<template>
  <el-container class="admin-view">
    <el-header>
      <router-link to="/admin" class="logo">
        <img src="~@/assets/img/logo.png" alt="">文档控制台
      </router-link>
      <div class="menu">
        <router-link class="item" to="/admin/document">文档管理</router-link>
        <router-link class="item" to="/admin/user" v-if="userInfo.has_privilege == 1">用户管理</router-link>
      </div>
      <div class="user">
        {{userInfo.username}}
        <div id="w7-nav-menu" class="menu-bar">
          <ul class="menu-ul">
            <li class="menu__item" @click="exit">退出系统</li>
          </ul>
        </div>
      </div>
    </el-header>
    <router-view></router-view>
  </el-container>
</template>

<script>
export default {
  name: 'admin',
  data() {
    return {
      userInfo: ''
    }
  },
  methods: {
    getUserInfo() {
      this.$post('/admin/user/getuser')
        .then(res => {
          this.userInfo = res
          sessionStorage.setItem("has_privilege",res.has_privilege)
        })
    },
    exit() {
      this.$router.push({
        name: 'adminLogin'
      })
      sessionStorage.removeItem("has_privilege")
      //清除cookie
      var d = new Date();
      d.setTime(d.getTime() + (-1*24*60*60*1000));
      var expires = "expires="+d.toUTCString();
      document.cookie = 'PHPSESSID' + "=" + '' + "; " + expires+"; path=/";//path=/是根路径
    }
  },
  created() {
    this.getUserInfo()
  }
}
</script>

